.hamburgerContainer {
	position: fixed;
	top: 0.5rem;
	left: 0.8rem;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 2rem;
	height: 2rem;
	background: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
	z-index: 1500;
}

.hamburgerContainer:focus {
	outline: none;
}

.hamburgerContainer div {
	width: 2rem;
	height: 0.25rem;
	background: black;
	border-radius: 10px;
	transition: all 0.3s linear;
	position: relative;
	transform-origin: 1px;
}

@media screen and (min-width: 640px) {
	.hamburgerContainer {
		display: none;
	}
}
